<template>
  <div
    class="product-gallery knockout-image"
    :class="{ [$class('loading')]: state === 'loading' }"
  >
    <!-- Main slider -->
    <div class="product-gallery__main knockout-image">
      <swiper
        v-show="hasImages"
        class="product-gallery__featured-carousel swiper-container knockout-image"
        :breakpoints="swiperSetting.breakpoint"
        :loop="true"
        :looped-slides="-1"
        :space-between="10"
        :modules="modules"
        :pagination="pagination"
        :thumbs="{ swiper: swiperThumbs }"
        @swiper="onSwiper"
        @update="handleUpdate"
      >
        <swiper-slide
          v-for="(image, index) in images"
          :id="`product-gallery-main-slide-${index}`"
          :key="image.src"
          class="product-gallery__featured-image"
        >
          <responsive-image
            :key="image.src"
            :image="image"
            :padding="false"
            min-max="900-1280"
            :swiper-lazyload="true"
            class="responsive-image__wrapper product-gallery__featured-image knockout-image"
          />
        </swiper-slide>

        <template v-if="models.length > 0">
          <swiper-slide
            v-for="(model, index) in models"
            :id="`product-gallery-main-slide-${modelIndex(index)}`"
            :key="model.preview_image.src"
            :data-swiper-slide-index="modelIndex(index)"
            class="product-gallery__featured-image"
          >
            <model-viewer
              :key="model.preview_image.src"
              :src="modelSource(model.sources, 'glb')"
              :ios-src="modelSource(model.sources, 'usdz')"
              :alt="model.alt"
              :poster="model.preview_image.src"
              :swiper-lazyload="true"
              ar="true"
              camera-controls="true"
              data-shopify-feature="1.12"
            />
          </swiper-slide>
        </template>
        <div
          class="
            product-gallery__pagination
            swiper-pagination
          "
        />
      </swiper>

      <slot
        v-if="!hasImages"
        name="galleryplaceholder"
      />
    </div>
    <!-- Thumbnails -->
    <div
      class="thumbnail-container"
    >
      <swiper
        v-if="template !== 'bundle'"
        v-show="!isMobile && hasImages"
        class="thumbnail-carousel swiper-container swiper-thumbs"
        :modules="modules"
        watch-slides-progress
        :free-mode="true"
        :loop="false"
        :active-class="'is-active'"
        :auto-height="true"
        :space-between="16"
        :direction="'vertical'"
        :navigation="navigation"
        :slides-per-view="3"
        :slide-to-clicked-slide="true"
        :breakpoints="thumbsSetting.breakpoints"
        @swiper="onSwiperThumbs"
        @update="handleThumbsUpdate"
      >
        <swiper-slide
          v-for="(image, index) in images"
          :id="`product-gallery-thumb-slide-${index}`"
          :key="image.src"
          ref="imageThumbnails"
          tabindex="0"
          :data-image-id="image.id"
          :data-slide="index"
          class="product-gallery__thumbnail"
        >
          <img
            :src="image.src"
            :alt="image.alt"
            class="product-gallery__thumbnail-image knockout-image"
          >
        </swiper-slide>

        <template v-if="models.length > 0">
          <swiper-slide
            v-for="(model, index) in models"
            :id="`product-gallery-thumb-slide-${modelIndex(index)}`"
            :key="model.preview_image.src"
            ref="imageThumbnails"
            tabindex="0"
            :data-image-id="model.id"
            :data-slide="modelIndex(index)"
            class="product-gallery__thumbnail"
          >
            <img
              :src="model.preview_image.src"
              :alt="model.alt"
              class="product-gallery__thumbnail-image knockout-image"
            >
          </swiper-slide>
        </template>
      </swiper>
      <div
        v-show="!isMobile"
        class="product-gallery__navigation swiper-button-prev swiper-button product-gallery__navigation--prev"
        :aria-label="'Previous slide'"
      >
        <chevron-up />
      </div>
      <div
        v-show="!isMobile"
        class="product-gallery__navigation swiper-button-next swiper-button product-gallery__navigation--next"
        :aria-label="'Next slide'"
      >
        <chevron-down />
      </div>
    </div>
  </div>
</template>

<script>

/**
 * Vue: Product gallery
 * -----------------------------------------------------------------------------
 * Product gallery component.
 *
 * @param {String|Boolean} color - Currently active color.
 * @param {Array} images - Gallery images.
 * @param {Boolean} mobile - Display mobile layout only.
 * @param {String} state - Parent template's loading state.
 *
 */
import ResponsiveImage from '~global/images/responsive-image'

import { A11y, FreeMode, Lazy, Navigation, Pagination, Thumbs } from 'swiper'
import { Swiper, SwiperSlide } from 'swiper/vue'

import ChevronUp from '~icons/direction/arrow/chevron-left.svg'
import ChevronDown from '~icons/direction/arrow/chevron-right.svg'

import { mapState } from 'vuex'

export default {
  name: 'ProductGallery',

  components: {
    ResponsiveImage,
    Swiper,
    SwiperSlide,
    ChevronUp,
    ChevronDown,
  },

  props: {
    color: {
      type: [String, Boolean],
      required: false,
      default: false,
    },
    images: {
      type: [Array, null],
      required: true,
      default: () => [],
    },
    media: {
      type: [Array],
      required: true,
      default: () => [],
    },
    state: {
      type: String,
      default: 'loading',
    },
    template: {
      type: String,
      default: '',
    },
    selectedVariant: {
      type: [Boolean, Object],
      default: false,
      required: true,
    },
  },

  setup(props) {
    const swiperSetting = {
      breakpoints: {
        1024: {
          noSwiping: true,
          noSwipingSelector: '.swiper-slide',
        },
      },
    }

    const thumbsSetting = {
      breakpoints: {
        1280: {
          slidesPerView: 5,
        },
      },
    }

    const navigation = {
      nextEl: '.product-gallery__navigation--next',
      prevEl: '.product-gallery__navigation--prev',
    }

    const pagination = {
      bulletActiveClass: 'product-gallery__bullet--active swiper-pagination-bullet-active',
      bulletClass: 'product-gallery__bullet swiper-pagination-bullet',
      clickable: true,
      currentClass: 'product-gallery__bullet--current',
      el: '.product-gallery__pagination',
      modifierClass: 'product-gallery__',
      type: 'bullets',
      renderBullet: (index, className) => {
        return `
          <span class="${className} ${className}_${index}"></span>
        `
      },
    }

    const modules = [
      A11y,
      FreeMode,
      Lazy,
      Navigation,
      Pagination,
      Thumbs,
    ]

    return {
      modules,
      navigation,
      pagination,
      swiperSetting,
      thumbsSetting,
      A11y,
      FreeMode,
      Lazy,
      Thumbs,
    }
  },

  data() {
    return {
      models: [],
      swiper: null,
      swiperThumbs: null,
    }
  },

  computed: {

    /**
     * Map Vuex state.
     */
    ...mapState({
      isMobile: (state) => state.index.screen.mobile,
    }),

    /**
     * Compute has images.
     */
    hasImages() {
      return this.images?.length > 0
    },
  },

  watch: {

    /**
     * Watch for color to updated.
     * - Resets slider back to index 0
     */
    color() {
      if (!this.swiper || typeof this.swiper.slideTo !== 'function') {
        return
      }

      this.swiper.slideTo(0)
    },

    selectedVariant() {
      if (!this.$refs?.imageThumbnails) {
        return
      }

      this.updateVariantImage()
    },
  },

  mounted() {
    this.getMediaModel()
  },

  methods: {

    /**
     * Save swiper instance.
     */
    onSwiper(swiper) {
      this.swiper = swiper
    },

    /**
     * Save swiper instance.
     */
    onSwiperThumbs(swiper) {
      this.swiperThumbs = swiper
    },

    /**
     * Handle Swiper update to force lazy load images.
     */
    handleUpdate() {
      this.swiper.lazy.load()
    },

    /**
     * Handle Swiper update to force lazy load images.
     */
    handleThumbsUpdate() {
      this.swiperThumbs.lazy.load()
      this.updateVariantImage()
    },

    updateVariantImage() {
      const thumbnails = this.$refs?.imageThumbnails

      if (!thumbnails) {
        return
      }

      const currentIndex = this.swiperThumbs.realIndex

      if (!thumbnails[currentIndex]) {
        return
      }

      const currentSlideImage = thumbnails[currentIndex].$attrs

      if (currentSlideImage['data-image-id'] === this.selectedVariant.image) {
        return
      }

      const matchingImage = thumbnails.find((image) => {
        const featuredImage = this.selectedVariant.featured_image?.id || this.selectedVariant.image
        return image.$attrs['data-image-id'] === featuredImage
      })

      if(!matchingImage) {
        return
      }

      this.swiperThumbs.realIndex = matchingImage.$attrs['data-slide']
      this.swiperThumbs.slideTo(matchingImage.$attrs['data-slide'])
      this.swiper.slideTo(matchingImage.$attrs['data-slide'])
    },

    /**
     * Get media type model
     */
    getMediaModel() {
      const mediaModel = this.media.filter(media => media.media_type == 'model')
      this.models = mediaModel
      this.handleUpdate()
    },

    /**
     * Get model source based on format
     * @returns {String}
     */
    modelSource(source, format) {
      if (!this.models) {
        return
      }

      const matchingSource = source.find((src) => src.format === format)
      return matchingSource?.url || ''
    },

    /**
     * Get slider index for models
     * @returns {Number}
     */
    modelIndex(index) {
      if (!this.images) {
        return
      }

      return index + this.images.length
    },
  },
}
</script>

<style lang="scss">
@import './product-gallery';
</style>
